import React from "react";
import styles from "./MainNav.module.scss";

const MainNav = ({ links, onNavigate, activePath }) => {
  const renderDropdown = (dropdown) => (
    <div className={styles.megaNav}>
      <div className={styles.subNav}>
        <div className={styles.col}>
          <div className={styles.text}>
            <h3>{dropdown.title}</h3>
            {dropdown.hasDescription && <p>{dropdown.description}</p>}
          </div>
          <div className={styles.subNavLinks}>
            {dropdown.links.map((subLink, subIndex) => (
              <a
                key={subIndex}
                href="#"
                onClick={() => onNavigate(subLink.path)}
              >
                {subLink.label}
              </a>
            ))}
          </div>
        </div>
      </div>
    </div>
  );

  return (
    <div className={styles.nav}>
      <ul>
        {links.map((link, index) => (
          <li key={index}>
            <a
              className={`${styles.link} ${
                link.path === activePath ? styles.active : ""
              }`}
              onClick={() => onNavigate(link.path)}
            >
              {link.label}
            </a>
            {link.dropdown && renderDropdown(link.dropdown)}
          </li>
        ))}
      </ul>
    </div>
  );
};

export default MainNav;
